import CButtonTip from '../packages/ButtonTip'

const components = [
  CButtonTip
]

const install = function(Vue, opt = {}) {
  components.forEach(component => {
    Vue.component(component.name, component)
  })
}
/* 支持使用标签的方式引入 */
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue);
}

export default {
  install,
  CButtonTip
}